<template>
  <div id="app">
    <GeneralHeader />
    <div class="container_">

      <div class="container" v-if="!isOnline">
        <div class="row">
          <div class="col-xs-12">
            <br>
            <h1>Internet not found</h1>
            <p>
              Looks like we weren't able to connect to our server. Please try again in a few minutes.
            </p>
            <hr>
          </div>
        </div>
        <div class="row">
          <div class="col-xs-12">
            <br>
            <button class="btn btn-lg btn-primary btn-block">Try again</button>
          </div>
        </div>
      </div>

      <div class="container" v-if="!isLocationAvailable">
        <div class="row">
          <div class="col-xs-12">
            <br>
            <h1>Location not found</h1>
            <p>
              Please turn on Location Services. For the best experience, please set it to High Accuracy
            </p>
            <hr>
          </div>
        </div>
        <div class="row">
          <div class="col-xs-12">
            <br>
            <button class="btn btn-lg btn-primary btn-block" @click="refreshContent">Refresh content</button>
          </div>
        </div>
      </div>

      <router-view v-if="isLocationAvailable" />
      <br>
      <br>
    </div>
    <GeneralFooter />
    <fab v-if="(currentCart.items.length  > 0)"
         :main-icon="fabMainIcon"
         :actions="fabActions"
         :position="fabPosition"
         :bg-color="fabBgColor"
         @gotoWallet="gotoWallet"
         @gotoCart="gotoCart"
         @gotoHome="gotoHome"
    ></fab>
    <fab v-if="(currentCart.items.length <= 0)"
         :main-icon="fabMainIcon"
         :actions="fabActionsWOCart"
         :position="fabPosition"
         :bg-color="fabBgColor"
         @gotoWallet="gotoWallet"
         @gotoCart="gotoCart"
         @gotoHome="gotoHome"
    ></fab>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

import { SET_APP_STATE } from "@/store/mutations.type";

import GeneralHeader from "@/components/GeneralHeader";
import GeneralFooter from "@/components/GeneralFooter";

import fab from 'vue-fab';

export default {
  name: 'App',
  components: {
    GeneralHeader,
    GeneralFooter,
    fab
  },
  data () {
    return {
      isLocationAvailable: false,
      fabMainIcon: 'pages',
      fabBgColor: '#ffba00',
      fabPosition: 'bottom-right',
      fabActions: [
          {
            name: 'gotoHome',
            icon: 'home'
          },
          {
            name: 'gotoWallet',
            icon: 'monetization_on'
          },
          {
            name: 'gotoCart',
            icon: 'shopping_cart'
          },
      ],
      fabActionsWOCart: [
          {
            name: 'gotoHome',
            icon: 'home'
          },
          {
            name: 'gotoWallet',
            icon: 'monetization_on'
          },
      ]
    }
  },

  mounted () {
    this.refreshContent();
  },

  methods: {
    gotoWallet(){
      this.$router.push({ name: 'wallet', })
    },
    gotoCart(){
      this.$router.push({ name: 'cart', })
    },
    gotoHome(){
      this.$router.push({ name: 'home', })
    },

    

    refreshContent() {
      this.isLocationAvailable = true;
      this.geolocation()
    },
    
    geolocation() {
      navigator.geolocation.getCurrentPosition(this.buildUrl, this.geoError);
    },

    buildUrl(position) {
      this.locationCoord.lat = position.coords.latitude;
      this.locationCoord.lon = position.coords.longitude;

      this.$store.commit(SET_CURRENT_LOCATION, {
        lat: this.locationCoord.lat,
        lon: this.locationCoord.lon,
      }, { root: true });
    },

    geoError(error) {
      this.error = error.code

      this.isLocationAvailable = false;
      
      switch(error.code) {
          case error.PERMISSION_DENIED:
              this.error =  "User denied the request for Geolocation."
              break;
          case error.POSITION_UNAVAILABLE:
              this.error =  "Location information is unavailable."
              break;
          case error.TIMEOUT:
              this.error =  "The request to get user location timed out."
              break;
          case error.UNKNOWN_ERROR:
              this.error =  "An unknown error occurred."
              break;
      }

      console.log(this.error)
    },

    getLocation() {
      return GeoService.getCurrentCoordinates().then(function(value) {
        // console.log('[STASH]LOCATION:', value.latitude, value.longitude);
        // this.fetchMerchants(value.latitude, value.longitude);
        // this.$store.dispatch(FETCH_MERCHANTS);
        return  { lat: value.latitude, long: value.longitude }
      }).catch(function(error) {
        // alert("Please turn on Location Services. For the best experience, please set it to High Accuracy")
        console.log(error);
        return null;
      });
    },

  },
  computed: {
    ...mapGetters(["currentCart"])
  },
}
</script>

<style scoped>
.container_{
  margin-top: 78px;
}
</style>


