<template>
	<div class="container">
		<div class="row">
			<div class="col-xs-12">
				<h1>Settings</h1><hr>
				<form @submit.prevent="updateSettings();">
					<fieldset>	
						<fieldset class="form-group">
							Name
							<input
							class="form-control form-control-lg"
							type="text"
							v-model="currentUser.username"
							@click="contactStashAsia"
							placeholder="Your username"
							disabled
							/>
						</fieldset><br>
						<fieldset class="form-group">
							Email
							<input
							class="form-control form-control-lg"
							type="text"
							v-model="currentUser.email"
							placeholder="Email"
							@click="contactStashAsia"
							disabled
							/>
						</fieldset><br>
						<fieldset class="form-group">
							Passsword
							<input
							class="form-control form-control-lg"
							type="password"
							v-model="currentUser.password"
							placeholder="Password"
							/>
						</fieldset><br>
						<fieldset class="form-group">
							Repeat Passsword
							<input
							class="form-control form-control-lg"
							type="password"
							v-model="currentUser.password"
							placeholder="Password"
							/>
						</fieldset>
						<button class="btn btn-lg btn-primary pull-xs-right">
							Update Settings
						</button>	
					</fieldset>
				</form>
				<hr>
				<button @click="updateApp" class="btn btn-outline-success btn-block btn-lg">
					App update
				</button>
				<br>
				<br>
				<br>
				<button @click="logout" class="btn btn-outline-danger btn-block">
					Logout
				</button>
			</div>
		</div>
	</div>
</template>

<script>
	import { mapGetters } from "vuex";
	import { LOGOUT } from "@/store/actions.type";

	export default {
	  name: "viewSettings",
	  computed: {
		...mapGetters(["currentUser"])
	  },
	  methods: {
			contactStashAsia() {
				alert("Please contact our hello@stash.asia")
			},
			updateSettings() {
				this.$store.dispatch(UPDATE_USER, this.currentUser).then(() => {
					// #todo, nice toast and no redirect
					this.$router.push({ name: "home" });
				});
			},
	  	logout () {
				if (window.confirm('Are you sure to log out your STASH account?')) {
					this.$store.dispatch(LOGOUT).then(() => {
						this.$router.push({ name: "home" });
					});
				}else{
					return 0;
				}

			},
			updateApp () {
				window.location.reload(true) 
			}
	  }
	};
</script>



